
.movieApp {
  background-color: #f0f0f0;
  height: 200px;
  text-align: center;
  padding: 20px;
}

.movieApp h1 {
  font-size: 50px;
  font-weight: 800;
  font-family: 'Roboto', sans-serif;
  color: #333;
  margin-bottom: 20px;
}

.searchInput {
  margin: 20px 0;
  background-color: #f0f0f0;
  margin-bottom: 40px;
}

.searchInput input {
  width: 300px;
  height: 40px;
  margin-right: 10px;
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  transition: border-color 0.3s;
}

.searchInput input:focus {
  border-color: #007bff;
  outline: none;
}

.searchInput button {
  width: 120px;
  height: 40px;
  border-radius: 5px;
  border: none;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.searchInput button:hover {
  background-color: #0056b3;
}

.movie-card {
  width: 300px;
  height: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  margin: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.movie-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.movie-card h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.movie-card p {
  font-size: 15px;
  margin-bottom: 10px;
}

.movie-card img {
  width: 250px;
  height: 250px;
  border-radius: 10px;
  object-fit: cover;
}


@media(max-width:500px) {
  .movieApp {
    background-color: #f0f0f0;
    height: 130px;
    text-align: center;
    padding: 20px;
  }
  .movieApp h1 {
    font-size: 30px;
    font-weight: 800;
    font-family: 'Roboto', sans-serif;
    color: #333;
    margin-bottom: 20px;
  }
  .searchInput input {
    width: 200px;
    height: 26px;
    margin-right: 5px;
    padding: 3px 5px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
    transition: border-color 0.3s;
  }
  .searchInput button {
    width: 80px;
    height: 30px;
    border-radius: 5px;
    border: none;
    background-color: #007bff;
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  .movie-card {
    width: 160px;
    height: 200px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    margin: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .movie-card img {
    width: 120px;
    height: 120px;
    border-radius: 10px;
    object-fit: cover;
  }
  .movie-card h2 {
    font-size: 14px;
  }
  .movie-card p {
    font-size: 10px;
  }
  
}